'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { getAssets, getCategories, exportToCSV, type Asset } from '@/lib/utils'

interface ReportData {
  totalAssets: number
  totalCategories: number
  assetsByCategory: { [key: string]: number }
  lowStockItems: number
  conditionStats: {
    Baik: number
    'Rusak Ringan': number
    'Rusak Berat': number
    'Tidak Dapat Digunakan': number
  }
}

export default function ReportsPage() {
  const [reportData, setReportData] = useState<ReportData>({
    totalAssets: 0,
    totalCategories: 0,
    assetsByCategory: {},
    lowStockItems: 0,
    conditionStats: {
      Baik: 0,
      'Rusak Ringan': 0,
      'Rusak Berat': 0,
      'Tidak Dapat Digunakan': 0
    }
  })
  const [selectedCategory, setSelectedCategory] = useState<string>('all')
  const [selectedCondition, setSelectedCondition] = useState<string>('all')
  const [filteredAssets, setFilteredAssets] = useState<Asset[]>([])

  useEffect(() => {
    loadReportData()
  }, [])

  useEffect(() => {
    filterAssets()
  }, [selectedCategory, selectedCondition])

  const loadReportData = () => {
    try {
      const assets = getAssets()
      const categories = getCategories()

      const assetsByCategory: { [key: string]: number } = {}
      const conditionStats = {
        Baik: 0,
        'Rusak Ringan': 0,
        'Rusak Berat': 0,
        'Tidak Dapat Digunakan': 0
      }

      let lowStockItems = 0

      assets.forEach(asset => {
        // Count by category
        assetsByCategory[asset.category] = (assetsByCategory[asset.category] || 0) + 1
        
        // Count by condition
        conditionStats[asset.condition] = (conditionStats[asset.condition] || 0) + 1
        
        // Count low stock items
        if (asset.quantity <= 5) {
          lowStockItems++
        }
      })

      setReportData({
        totalAssets: assets.length,
        totalCategories: categories.length,
        assetsByCategory,
        lowStockItems,
        conditionStats
      })

      setFilteredAssets(assets)
    } catch (error) {
      console.error('Error loading report data:', error)
    }
  }

  const filterAssets = () => {
    try {
      const assets = getAssets()
      let filtered = assets

      if (selectedCategory !== 'all') {
        filtered = filtered.filter(asset => asset.category === selectedCategory)
      }

      if (selectedCondition !== 'all') {
        filtered = filtered.filter(asset => asset.condition === selectedCondition)
      }

      setFilteredAssets(filtered)
    } catch (error) {
      console.error('Error filtering assets:', error)
    }
  }

  const handleExportCSV = () => {
    try {
      const exportData = filteredAssets.map(asset => ({
        'ID': asset.id,
        'Nama Aset': asset.name,
        'Kategori': asset.category,
        'Jumlah': asset.quantity,
        'Kondisi': asset.condition,
        'Lokasi': asset.location || '-',
        'Tanggal Pembelian': asset.purchaseDate || '-',
        'Deskripsi': asset.description || '-'
      }))

      exportToCSV(exportData, `laporan-aset-${new Date().toISOString().split('T')[0]}.csv`)
    } catch (error) {
      console.error('Error exporting CSV:', error)
    }
  }

  const categories = getCategories()

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Laporan</h1>
          <p className="text-gray-600 mt-2">
            Analisis dan ekspor data aset Puskesmas Kresek
          </p>
        </div>
        <Button onClick={handleExportCSV}>
          Ekspor ke CSV
        </Button>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">
              Total Aset
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">{reportData.totalAssets}</div>
            <p className="text-xs text-gray-500 mt-1">
              Aset terdaftar dalam sistem
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">
              Kategori
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{reportData.totalCategories}</div>
            <p className="text-xs text-gray-500 mt-1">
              Kategori aset tersedia
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">
              Stok Rendah
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{reportData.lowStockItems}</div>
            <p className="text-xs text-gray-500 mt-1">
              Aset dengan stok ≤ 5
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">
              Kondisi Baik
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-purple-600">{reportData.conditionStats.Baik}</div>
            <p className="text-xs text-gray-500 mt-1">
              Aset dalam kondisi baik
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Filters */}
      <Card>
        <CardHeader>
          <CardTitle>Filter Laporan</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <label className="text-sm font-medium">Filter berdasarkan Kategori</label>
              <Select value={selectedCategory} onValueChange={setSelectedCategory}>
                <SelectTrigger>
                  <SelectValue placeholder="Pilih kategori" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Semua Kategori</SelectItem>
                  {categories.map(category => (
                    <SelectItem key={category.id} value={category.name}>
                      {category.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Filter berdasarkan Kondisi</label>
              <Select value={selectedCondition} onValueChange={setSelectedCondition}>
                <SelectTrigger>
                  <SelectValue placeholder="Pilih kondisi" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Semua Kondisi</SelectItem>
                  <SelectItem value="Baik">Baik</SelectItem>
                  <SelectItem value="Rusak Ringan">Rusak Ringan</SelectItem>
                  <SelectItem value="Rusak Berat">Rusak Berat</SelectItem>
                  <SelectItem value="Tidak Dapat Digunakan">Tidak Dapat Digunakan</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Results Summary */}
      <Card>
        <CardHeader>
          <CardTitle>
            Hasil Filter ({filteredAssets.length} aset ditemukan)
          </CardTitle>
        </CardHeader>
        <CardContent>
          {filteredAssets.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <p className="text-lg mb-2">Tidak ada aset yang sesuai dengan filter</p>
              <p className="text-sm">
                Coba ubah filter atau tambahkan aset baru
              </p>
            </div>
          ) : (
            <div className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="bg-blue-50 p-4 rounded-lg">
                  <p className="text-sm text-blue-800 font-medium">Total Nilai</p>
                  <p className="text-xl font-bold text-blue-900">
                    {filteredAssets.reduce((sum, asset) => sum + asset.quantity, 0)}
                  </p>
                </div>
                <div className="bg-green-50 p-4 rounded-lg">
                  <p className="text-sm text-green-800 font-medium">Rata-rata Stok</p>
                  <p className="text-xl font-bold text-green-900">
                    {(filteredAssets.reduce((sum, asset) => sum + asset.quantity, 0) / filteredAssets.length).toFixed(1)}
                  </p>
                </div>
                <div className="bg-purple-50 p-4 rounded-lg">
                  <p className="text-sm text-purple-800 font-medium">Kategori Terbanyak</p>
                  <p className="text-xl font-bold text-purple-900">
                    {Object.entries(
                      filteredAssets.reduce((acc, asset) => {
                        acc[asset.category] = (acc[asset.category] || 0) + 1
                        return acc
                      }, {} as { [key: string]: number })
                    ).sort((a, b) => b[1] - a[1])[0]?.[0] || '-'}
                  </p>
                </div>
              </div>

              <div className="bg-gray-50 p-4 rounded-lg">
                <p className="text-sm text-gray-800 font-medium mb-2">Distribusi Kondisi</p>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-2">
                  {Object.entries(
                    filteredAssets.reduce((acc, asset) => {
                      acc[asset.condition] = (acc[asset.condition] || 0) + 1
                      return acc
                    }, {} as { [key: string]: number })
                  ).map(([condition, count]) => (
                    <div key={condition} className="text-center">
                      <p className="text-lg font-bold">{count}</p>
                      <p className="text-xs text-gray-600">{condition}</p>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Category Distribution */}
      <Card>
        <CardHeader>
          <CardTitle>Distribusi Aset per Kategori</CardTitle>
        </CardHeader>
        <CardContent>
          {Object.keys(reportData.assetsByCategory).length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <p className="text-sm">Belum ada data kategori</p>
            </div>
          ) : (
            <div className="space-y-3">
              {Object.entries(reportData.assetsByCategory)
                .sort((a, b) => b[1] - a[1])
                .map(([category, count]) => (
                  <div key={category} className="flex items-center justify-between">
                    <span className="text-sm font-medium">{category}</span>
                    <div className="flex items-center space-x-2">
                      <div className="w-32 bg-gray-200 rounded-full h-2">
                        <div
                          className="bg-blue-600 h-2 rounded-full"
                          style={{ width: `${(count / reportData.totalAssets) * 100}%` }}
                        />
                      </div>
                      <span className="text-sm text-gray-600 w-8 text-right">{count}</span>
                    </div>
                  </div>
                ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
